import React, { Component } from "react";
import PropTypes from "prop-types";
import SpeechRecognition from "react-speech-recognition";

const propTypes = {
  // Props injected by SpeechRecognition
  transcript: PropTypes.string,
  resetTranscript: PropTypes.func,
  browserSupportsSpeechRecognition: PropTypes.bool
};

class Dictaphone extends Component {

  startMicroPhone = () =>{
    console.log('start from child');
    
    this.props.startListening();
  };

  
  componentWillReceiveProps(nextProps) {
  console.log("nextProps.call");

  // if(nextProps.call){
    this.startMicroPhone();
  // }
  
  }
  

  render() {
    const {
      transcript,
      resetTranscript,
      browserSupportsSpeechRecognition,
      finalTranscript,
      interimTranscript
    } = this.props;
    console.log("finalTranscript",finalTranscript);
    if (finalTranscript.includes("lighter")) {
      console.log("lighter");
      this.props.showLigheterResults();
      resetTranscript();
    }

    if (
      finalTranscript.includes("Deepa") ||
      finalTranscript.includes("deeper")
    ) {
      console.log("lighter");
      this.props.showNextDeeperResults();
      resetTranscript();
    }

    console.log("finalTranscript", finalTranscript);

    if (!browserSupportsSpeechRecognition) {
      return null;
    }

    return (
      <div>
        {/* <button onClick={resetTranscript}>Reset</button>
        <span style={{ color: "white" }}>{interimTranscript}</span>
        <div style={{ color: "white" }}>{finalTranscript}</div> */}
      </div>
    );
  }
}

Dictaphone.propTypes = propTypes;

const options = {
  autoStart: false,
  // continuous:false
};

export default SpeechRecognition(options)(Dictaphone);
